	      "title": "GraphQL client-side execution",
	      "speakerId": 8,
	      "description": ["Traditionally, GraphQL clients were focused on client-server communication and caching, with GraphQL query executor on the server. However, it's also possible to run full GraphQL executor on the client side.", "The talk will explore the concept of schema stitching(connecting multiple GraphQL schemas together) from a client - side point of view.Using this approach, developer can merge multiple remote schemas together with a frontend schema.The combined schema is responsible for routing requests to a server and a client-side storage/cache.", "The talk will explore both schema stitching implementation and the possible applications.Applications of this approach include prototyping application's GraphQL schema without a server. After prototype stage, the schema can be moved to the server. Another use-case is building frontend-only applications that use multiple public GraphQL APIs."],
	      "showReadMore": true
