	      "description": ["A year ago an unnamed investor asked me for a consultancy on topic: \"If this is possible to mine crypto in browser using GPU?\". The talk shares some insights / outcome found during the research and gives an idea of what to expect when mining cryptocurrencies using a pool of browser based miners."],
