	      "title": "The bumpy road to Universal JavaScript",
	      "speakerId": 6,
	      "description": ["Since the mid 90s, developers have struggled to build web applications that can be executed both on the client and the server. And still today, even with Node.js and virtual DOM libraries like React, this can be quite challenging.", "In this talk, we'll look at common obstacles and problems that developers face when building this type of web application, including typical performance and security issues. You'll leave the presentation knowing how to build a fast and secure web application than can be rendered on the client and the server."],
