	module.exports = {"all":[{"id":1,"firstName":"Lea","lastName":"Verou","position":"Human-Computer Interaction Researcher","company":"MIT CSAIL","description":"Lea is currently busy researching how to make web programming easier at <a href='http://www.csail.mit.edu/' target='_blank'>MIT CSAIL</a>. She is the author of bestselling advanced CSS book <a href='http://shop.oreilly.com/product/0636920031123.do' target='_blank'>CSS Secrets</a> and has worked as a Developer Advocate at <a href='https://www.w3.org/' target='_blank'>W3C</a>. She has a long-standing passion for open web standards, and is one of the few Invited Experts in the <a href='https://www.w3.org/Style/CSS/members.en.php3' target='_blank'>CSS Working Group</a>. Lea has also started <a href='https://github.com/leaverou' target='_blank'>several popular open source projects and web applications</a>, such as <a href='https://mavo.io/' target='_blank'>Mavo</a>, <a href='http://prismjs.com/' target='_blank'>Prism</a>, and <a href='http://leaverou.github.io/prefixfree/' target='_blank'>-prefix-free</a> and maintains a technical blog at <a href='http://lea.verou.me/' target='_blank'>lea.verou.me</a>. She holds a MSc in Computer Science from MIT. Despite her academic pursuits in Computer Science, Lea is one of the few misfits who love code and design equally.","imageSrc":"/speakers/lea_verou","anchor":"Lea-Verou","highlighted":true,"highlightedDescription":"Lea is currently busy researching how to make web programming easier at <a' href='http://www.csail.mit.edu/' target='_blank'>MIT CSAIL</a>. She is the author of bestselling advanced CSS book <a href='http://shop.oreilly.com/product/0636920031123.do' target='_blank'>CSS Secrets</a> and has worked as a Developer Advocate at <a href='https://www.w3.org/' target='_blank'>W3C</a>. She has a long-standing passion for open web standards, and is one of the few Invited Experts in the <a href='https://www.w3.org/Style/CSS/members.en.php3' target='_blank'>CSS Working Group</a>.","social":[{"type":"twitter","url":"https://twitter.com/LeaVerou"},{"type":"github","url":"https://github.com/leaverou"},{"type":"linkedin","url":"https://www.linkedin.com/in/leaverou/"}]},{"id":2,"firstName":"Benedikt","lastName":"Meurer","position":"Technical Lead","company":"Google","description":"Benedikt is a JavaScript compiler engineer who loves to tinker with different aspects of programming languages. <br />In 2013 he joined Google to work on the <a href='https://developers.google.com/v8/' target='_blank'>V8 JavaScript Engine</a> in the Munich office, where he is currently working as tech lead for the Node.js performance team. <br />He spent a rather large amount of his spare time on developing and contributing to open source projects. From 2002 to 2007, he developed and maintained various core parts of the <a href='https://xfce.org' target='_blank'>Xfce desktop environment</a>, including the new file manager <a href='https://docs.xfce.org/xfce/thunar/start' target='_blank'>Thunar</a>, which he wrote from scratch. <br />He is also interested in functional programming languages and compiler design. In 2011 he created a <a href='https://github.com/bmeurer/ocaml-arm/wiki' target='_blank'>brand new ARM port</a> for the <a href='https://caml.inria.fr/ocaml/' target='_blank'>OCaml</a> native code compiler, and since January 2012 he is now the official maintainer of the <a href='https://www.arm.com' target='_blank'>ARM</a> port.","highlightedDescription":"Benedikt is a JavaScript compiler engineer who loves to tinker with different aspects of programming languages. At Google he is working on the <a href='https://developers.google.com/v8/' target='_blank'>V8 JavaScript Engine</a> that powers both Node.js and Chrome, and he is currently leading the Node.js Performance Team.","imageSrc":"/speakers/benedikt_meurer","anchor":"Benedikt-Meurer","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/bmeurer"},{"type":"github","url":"https://github.com/bmeurer"},{"type":"linkedin","url":"https://www.linkedin.com/in/benediktmeurer/"}]},{"id":3,"firstName":"Chen Hui","lastName":"Jing","position":"Senior Front-end Developer","company":"Jewel Paymentech","description":"Hui Jing is a self-taught designer and developer from Singapore with an inordinate love of CSS. Reducing lines of code in her web projects makes her extremely happy. She used to play basketball full-time and launched her web career during downtime between training sessions. She also co-organises the <a href='https://singaporecss.github.io' target='_blank'>Talk.CSS Meetup</a>, runs an inspiring <a href='https://www.chenhuijing.com' target='_blank'>web-dev blog</a> and you have to check out her <a href='https://www.chenhuijing.com/talks' target='_blank'>list of talks</a> at numerous conferences and meetups.","highlightedDescription":"Hui Jing is a self-taught designer and developer from Singapore with an inordinate love of CSS. Reducing lines of code in her web projects makes her extremely happy. She used to play basketball full-time and launched her web career during downtime between training sessions.","imageSrc":"/speakers/chen_hui_jing","anchor":"Chen-Hui-Jing","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/hj_chen"},{"type":"github","url":"https://github.com/huijing"},{"type":"linkedin","url":"https://www.linkedin.com/in/huijingchen/"}]},{"id":4,"firstName":"Tomasz","lastName":"Łakomy","position":"Software Engineer","company":"Egnyte Poland","description":"Tomasz is a Software Engineer at Egnyte Poland. Co-organiser of React Poznań Meetup, JavaScript tinkerer who enjoys sharing his knowledge with other developers. His interests include VR, AR, React, app performance and making cheesiest jokes known to humanity.","highlightedDescription":"Tomasz is a Software Engineer at Egnyte Poland. Co-organiser of React Poznań Meetup, JavaScript tinkerer who enjoys sharing his knowledge with other developers. His interests include VR, AR, React, app performance and making cheesiest jokes known to humanity.","imageSrc":"/speakers/tomasz_lakomy","anchor":"Tomasz-Lakomy","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/tlakomy"},{"type":"github","url":"https://github.com/tlakomy"},{"type":"linkedin","url":"https://www.linkedin.com/in/tomasz-łakomy-12b2a258/"}]},{"id":5,"firstName":"Vitaly","lastName":"Friedman","position":"Editor-in-Chief","company":"Smashing Magazine","description":"Vitaly Friedman loves beautiful content and does not give up easily. From Minsk in Belarus, he studied computer science and mathematics in Germany, discovered the passage a passion for typography, writing and design. After working as a freelance designer and developer for 6 years, he co-founded <a href='https://smashingmagazine.com' target='__blank'>Smashing Magazine</a>, a leading online magazine dedicated to design and web development. Vitaly is the author, co-author and editor of all <a href='https://shop.smashingmagazine.com' target='__blank'>Smashing Books</a>.","highlightedDescription":"Vitaly Friedman loves beautiful content and does not give up easily. He currently works as editor-in-chief of <a href='https://smashingmagazine.com' target='__blank'>Smashing Magazine</a> in the lovely city of Freiburg, Germany. Vitaly is the author, co-author and editor of all <a href='https://shop.smashingmagazine.com' target='__blank'>Smashing Books</a>.","imageSrc":"/speakers/vitaly_friedman","anchor":"Vitaly-Friedman","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/smashingmag"},{"type":"github","url":"https://github.com/vitalyfriedman"},{"type":"linkedin","url":"https://www.linkedin.com/in/vitalyfriedman/"}]},{"id":6,"firstName":"Johannes","lastName":"Ewald","position":"Core Team Member","company":"Webpack","description":"Passionate <a href='https://github.com/jhnns' target='_blank'>Open Source developer</a>. Founded <a href='https://peerigon.com/' target='_blank'>a company</a> with his friends to develop challenging web projects with JavaScript. Loves music and nature. Member of the <a href='https://webpack.js.org/' target='_blank'>Webpack</a> core team.","highlightedDescription":"Passionate <a href='https://github.com/jhnns' target='_blank'>Open Source developer</a>. Founded <a href='https://peerigon.com/' target='_blank'>a company</a> with his friends to develop challenging web projects with JavaScript. Loves music and nature. Member of the <a href='https://webpack.js.org/' target='_blank'>Webpack</a> core team.","imageSrc":"/speakers/johannes_ewald","anchor":"Johannes-Ewald","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/jhnnns"},{"type":"github","url":"https://github.com/jhnns"}]},{"id":7,"firstName":"Juho","lastName":"Vepsäläinen","position":"Core Team Member","company":"Webpack","description":"Juho Vepsäläinen is behind the <a href='https://survivejs.com/' target='_blank'>SurviveJS</a> effort. In addition to being a core developer of <a href='https://webpack.js.org/' target='_blank'>Webpack</a>, he has been active in the Open Source scene since the early 2000s. Blue Arrow Awards <a href='https://survivejs.com/blog/blue-arrow-awards-winner-2017/' target='_blank'>winner</a>.","highlightedDescription":"Juho Vepsäläinen is behind the <a href='https://survivejs.com/' target='_blank'>SurviveJS</a> effort. In addition to being a core developer of <a href='https://webpack.js.org/' target='_blank'>Webpack</a>, he has been active in the Open Source scene since the early 2000s. Blue Arrow Awards <a href='https://survivejs.com/blog/blue-arrow-awards-winner-2017/' target='_blank'>winner</a>.","imageSrc":"/speakers/juho_vepsalainen","anchor":"Juho-Vepsalainen","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/bebraw"},{"type":"github","url":"https://github.com/bebraw"},{"type":"linkedin","url":"https://www.linkedin.com/in/juhovepsalainen/"}]},{"id":8,"firstName":"Mikhail","lastName":"Novikov","position":"CTO, Co-founder","company":"Reindex","description":"Mikhail is a CTO and Co-founder at Reindex. He started <a href='https://www.reindex.io/' target='_blank'>Reindex</a> as a first GraphQL-focused startup in the world and built first GraphQL Backend-as-a-Service. After BaaS closed, Mikhail has been doing GraphQL consulting. He has also been active in GraphQL open source projects, such as Launchpad and graphql-tools. Before Reindex, Mikhail has been doing web development consulting. He jumped onto React train early and never looked back. Even before that, he used to be a developer at several startups, where he programmed recycling robots and messaging apps in the age before WhatsApp and Telegram.","highlightedDescription":"Mikhail is a CTO and Co-founder at Reindex. He started <a href='https://www.reindex.io/' target='_blank'>Reindex</a> as a first GraphQL-focused startup in  the world and built first GraphQL Backend-as-a-Service. After BaaS closed, Mikhail has been doing GraphQL consulting. He has also been active in GraphQL open source projects, such as Launchpad and graphql-tools. ","imageSrc":"/speakers/mikhail_novikov","anchor":"Mikhail-Novikov","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/freiksenet"},{"type":"github","url":"https://github.com/freiksenet"},{"type":"linkedin","url":"https://www.linkedin.com/in/freiksenet/"}]},{"id":9,"firstName":"Denis","lastName":"Radin","position":"JavaScript Engineer","company":"Evolution Gaming","description":"JavaScript engineer working on photorealistic 3d games at Evolution Gaming. Passionate about performance optimization and code aesthetics, mastering workshop <a href='http://challengingnative.com' target='_blank'>http://challengingnative.com</a>, hosting <a href='https://react.amsterdam/' target='_blank'>https://react.amsterdam</a>, <a href='http://amsterdamjs.com/' target='_blank'>http://amsterdamjs.com</a> as well as running super interesting technical <a href='http://pixelscommander.com/' target='_blank'>blog</a>.","highlightedDescription":"JavaScript engineer working on photorealistic 3d games at Evolution Gaming. Passionate about performance optimization and code aesthetics, mastering workshop <a href='http://challengingnative.com' target='_blank'>http://challengingnative.com</a>, <a href='https://react.amsterdam/' target='_blank'>https://react.amsterdam</a>, <a href='http://amsterdamjs.com/' target='_blank'>http://amsterdamjs.com</a>.","imageSrc":"/speakers/denis_radin","anchor":"Denis-Radin","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/pixelscommander"},{"type":"github","url":"https://github.com/PixelsCommander"},{"type":"linkedin","url":"https://www.linkedin.com/in/denisradin/"}]},{"id":10,"firstName":"Andrey","lastName":"Okonetchnikov","position":"Front-end developer and UI/UX designer","company":"","description":"Andrey is a front-end engineer and UI designer from Russia who lives in Austria and works on modern user interfaces using web technologies. As an active Open Source contributor, he contributed to Webpack, React.js, and Jest; designed the PostCSS official website and created a bunch of logos for other OSS projects.<br/> Andrey is a co-organizer of the ReactVienna community, designer, and co-creator of ColorSnapper. He loves mountain biking, snowboarding, and specialty coffee.","highlightedDescription":"Andrey is a front-end engineer and UI designer from Russia who lives in Austria and works on modern user interfaces using web technologies. As an active Open Source contributor, he contributed to Webpack, React.js, and Jest; designed the PostCSS official website and created a bunch of logos for other OSS projects.","imageSrc":"/speakers/andrey_okonetchnikov","anchor":"Andrey-Okonetchnikov","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/okonetchnikov"},{"type":"github","url":"https://github.com/okonet"}]},{"id":11,"firstName":"Benedek","lastName":"Gagyi","position":"Developer","company":"LogMeIn","description":"Developer, teacher, sports-fanatic. As a developer, he's working on simplifying online collaboration at <a href='https://www.join.me' target='_blank'>join.me</a> by LogMeIn. As a teacher, he's training people in various frontend related fields at <a href='https://www.training360.com/en' target='_blank'>Training360</a>.<br/>In his free time, he's researching the patterns and algorithms connecting basketball, trail running, and drums.<br/>His devotion to impeccable user interfaces is rivaled only by his constant search for the perfect brew of Chinese tea.","highlightedDescription":"Developer, teacher, sports-fanatic. As a developer, he's working on simplifying online collaboration at <a href='https://www.join.me' target='_blank'>join.me</a> by LogMeIn. As a teacher, he's training people in various frontend related fields at <a href='https://www.training360.com/en' target='_blank'>Training360</a>.","imageSrc":"/speakers/benedek_gagyi","anchor":"Benedek-Gagyi","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/benedekgagyi"},{"type":"github","url":"https://github.com/gagyibenedek"}]},{"id":12,"firstName":"Michaela","lastName":"Lehr","position":"Front-end Developer and Designer","company":"GeilDanke","description":"Michaela is a creative front-end developer and designer. She specializes in UX and WebVR and co-founded the studio <a href='https://geildanke.com/en/' target='_blank'>GeilDanke</a>.<br />In her free time, she travels around the world, practices yoga, and watches way too much Buffy.","highlightedDescription":"Michaela is a creative front-end developer and designer. She specializes in UX and WebVR and co-founded the studio <a href='https://geildanke.com/en/' target='_blank'>GeilDanke</a>.","imageSrc":"/speakers/michaela_lehr","anchor":"Michaela-Lehr","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/FischaelaMeer"},{"type":"facebook","url":"https://www.facebook.com/michaela.lehr"}]},{"id":13,"firstName":"Ronen","lastName":"Amiel","position":"Software Developer","company":"Wix","description":"Ronen is a software developer at Wix in the Infra team. He’s working on creating JavaScript tools for developers. Currently, he is busy working on a zero-configuration toolkit, similar to <a href='https://github.com/facebook/create-react-app/' target='_blank'>Create React App</a>, that helps developers bootstrap their projects.<br />Passionate about playing computer games, Ronen started his way by developing his own games with ActionScript. He also loves to understand how things actually work and create simplified versions that show how they are implemented.<br />In his free time, he likes fiddling with functional programming and Haskell in particular, running, rope jumping and playing board games.","highlightedDescription":"Ronen is a software developer from Israel. In the Infra team at Wix, he’s working on creating JavaScript tools for developers. Currently, he is busy working on a zero-configuration toolkit, similar to <a href='https://github.com/facebook/create-react-app/' target='_blank'>Create React App</a>, that helps developers bootstrap their projects.<br />Passionate about playing computer games, Ronen started his way by developing his own games with ActionScript. He also loves to understand how things actually work and create simplified versions that show how they are implemented.","imageSrc":"/speakers/ronen_amiel","anchor":"Ronen-Amiel","highlighted":true,"social":[{"type":"twitter","url":"https://twitter.com/ronenamiel"},{"type":"facebook","url":"https://github.com/ronami"}]}]}

